import { connect } from "react-redux";
import { changeSearch, searchSwitcher } from "../../Redux/header-reducer";
import Header from "./header";


let mapStateToProps = (state) => ({
    classAction: state.headerReducer.classAction,
    searchValue: state.headerReducer.searchValue,
})

export default connect(mapStateToProps, {searchSwitcher, changeSearch})(Header);