const SEARCH_SWITCH = 'SEARCH_SWITCH';
const CHANGE_SEARCH = 'CHANGE_SEARCH';


let initialState = {
    classAction: '',
    searchValue: '',
}

const headerReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEARCH_SWITCH:
            return {...state, classAction: action.classAction,}
        case CHANGE_SEARCH: 
            return {...state, searchValue: action.value }
        default:
            return state;
    }
}

export const searchSwitcher = (classAction) => ({type: SEARCH_SWITCH, classAction});
export const changeSearch = (value) => ({type: CHANGE_SEARCH, value});

export default headerReducer;